<template>
  <v-app style="margin: 0;">
    <v-card style=" padding: 0 40px 0 40px">
      <v-card-title></v-card-title>
      <!---->
      <v-text-field
        v-model="username"
        label=""
        :rules="[rules.required,rules.usernamemax,rules.usernameCheck]"
        @click:append="showpassword = !showpassword"
      ></v-text-field>
      <!---->
      <v-text-field
        v-model="userpassword"
        :append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
        :type="showpassword ? 'text' : 'password'"
        label=""
        :rules="[rules.required,rules.min,rules.passwordmax]"
        @click:append="showpassword = !showpassword">
      </v-text-field>
      <!---->
      <v-text-field
        v-model="passwordmatch"
        :append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
        :type="showpassword ? 'text' : 'password'"
        :rules="[rules.sameCheck(passwordmatch,userpassword)]"
        label=""
        @click:append="showpassword = !showpassword">
      </v-text-field>
      <!---->
      <v-text-field
        v-model="emailAddr"
        label=""
        :rules="[rules.emailCheck(emailAddr)]"
        @click:append="showpassword = !showpassword"
      ></v-text-field>
      <v-card-actions>
        <!---->
        <v-btn :disabled="!registerValid"
               @click="registerOpr"></v-btn>
      </v-card-actions>
    </v-card>
  </v-app>
</template>

<script>

    export default {
        name: "register",
      data:()=>({
        rules: {
          required: value => !!value || '',
          min: v => v.length >= 8 || '8',
          usernamemax: v => v.length < 20 || '20',
          passwordmax: v => v.length < 72 || '72',
          sameCheck:function (value,password) {
            if (value !== password) return ('')
            else return true
          },
          usernameCheck:function () {
            return true
          },
          emailCheck:function (emailstr) {
            if (/.+@.+\..+/.test(emailstr)) return true
            else return ('')
          },
        },
        emailAddr:'',
        username:'',
        userpassword:'',
        passwordmatch:'',
        matchmessage:'',
        showpassword:false,
        errormessage:''
      }),
      computed:{
          //
          registerValid(){
            return(this.username!=='' && this.userpassword === this.passwordmatch
              && this.userpassword !== '' && !(this.rules.emailCheck(this.emailAddr)==='')
            && this.emailAddr !== '' && this.rules.usernamemax(this.username)
            && this.rules.passwordmax(this.password))
          }
      },
      methods:{
          //
          registerOpr() {
            //
            this.$axios.post('api/register', {
              username: this.username,
              password: this.userpassword,
              email: this.emailAddr,
            })
              .then(function (response) {
                console.log(response);
                if (response) {
                  alert('')
                  this.$router.push('/')
                }
                else {
                  if (response.error === 'usernotfound') {
                    alert('')
                  }
                  if (response.error === 'usernotfound') {
                    alert('')
                  }
                }
              })
              .catch(function (error) {
                alert('')
                console.log(error);
              });
            //
            alert('')
            this.$router.push('/')
          }
      }
    }
</script>

<style scoped>

</style>



