<template>
  <v-app style="margin: 0;">
    <!---->
      <v-navigation-drawer
        :color="'primary'"
        height="1200"
        width="300"
        permanent absolute dark>
        <v-list dense nav class="py-0">

          <!---->
          <v-list-item two-line>
            <!---->
            <v-list-item-avatar>
              <img src=userimgUrl alt="userimg">
            </v-list-item-avatar>
            <!---->
            <v-list-item-content style="height: 110px">
              <v-list-item-title style="height: 35px" class="text-h6">{{username}}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>

          <!---->
          <v-list-item
            v-for="(item,index) in dashboardItems"
            :key="item.title"
            style="height: 110px;"
            :to="item.routerset">
            <div style="display: flex; justify-content: center;">
              <!---->
              <v-list-item-icon><v-icon>{{ item.icon }}</v-icon></v-list-item-icon>
              <!---->
              <v-list-item-content style="padding: 0;">
                <div style="height: 50px;" class="text-h6">
                  <p>{{ item.title }}</p>
                </div>
              </v-list-item-content>
            </div>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    <!---->
      <v-sheet>
        <router-view></router-view>
      </v-sheet>
  </v-app>
</template>

<script>

    export default {
      name: "users",

      data:()=>({

        username:'',

        //
        dashboardItems: [
          {title: '', icon: 'mdi-view-dashboard', routerset:{path:'/users/userInfo'}},
          {title: '', icon: 'mdi-book',routerset:{path:'/users/myblogs'}},
          {title: '', icon: 'mdi-pen',routerset:{path:'/users/editblogs',query:{mode:'new'}}},
        ],//


        //
        scrheight:window.innerHeight,//


      }),
      mounted() {
        const that=this;





        //
        window.onresize=()=>{
          that.scrheight=window.innerHeight;
        }
      },
      computed:{
        //
        //
        docheight(){
          return document.body.clientHeight
        },
      },
      beforeDestroy() {
        //
        this.$bus.off('userlogin')
      },
      methods:{


        //
        delBlogs(articleindex){
          this.blogs.splice(articleindex,1)
          this.delBlogsConfirmShow = false
        },








      }
    }
</script>

<style scoped>

</style>



