<template>
  <v-card style="padding-left: 300px;">
    <v-card-title>
      {{this.mode==='new'?'':''}}
    </v-card-title>

    <v-row>
      <v-col cols="1" class="align-self-baseline" ></v-col>
      <v-col cols="6">
        <v-text-field v-model="this.editedItem.title"></v-text-field>
      </v-col>
      <v-col cols="5">:{{editedItem.censor}}</v-col>
    </v-row>

    <mavon-editor
      v-model="this.editedItem.content"
      ref="md"
      @change="mdTextChange"
      @imgAdd="imgAdd"
      style="min-height: 600px"
    />
    <v-btn @click="mdTextSubmit"></v-btn>
  </v-card>
</template>

<script>
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
    export default {
        name: "editblogs",
      components: {
        mavonEditor,//md
      },
      data:()=>({
        editedItem:[],//
        //md
        mdHtml:'',    // mdmdHtml
        mode:'',
        newBlogTitle: '',
      }),
      mounted() {
        this.mode = this.$route.query.mode
        if (this.mode === 'edit') {
          this.editedItem = this.$route.query.item
        }
      },
      methods:{
        // md
        mdTextSubmit(){

          //
          if (this.mode === 'new'){
            this.$axios.post('api/article/new',{
              title: this.editedItem.title,
              content: this.editedItem.content,
            }).then(function (response) {
              alert(response)
              console.log(response)
            }).catch(function (error) {
              alert('')
            })
          }
          //
          else if (this.mode === 'edit') {
            this.$axios.post('api/article/modify', {
              title: this.editedItem.title,
              content: this.editedItem.content,
              id:this.$route.query.id,
            }).then(function (response) {
              alert(response)
              console.log(response)
            }).catch(function (error) {
              alert('')
            })
          }


        },

        //@change
        mdTextChange(value, render){
          // render  markdown [mdHtml]
          this.mdHtml = render;
        },

        //md
        imgAdd(pos, $file){
          let formdata = new FormData();
          this.$upload.post('http://127.0.0.1:6666', formdata).then(res => {
            console.log(res.data);
            this.$refs.md.$img2Url(pos, res.data);
          }).catch(err => {
            console.log(err)
          })
        },
      }
    }
</script>

<style scoped>

</style>



