<template>
  <v-card style="padding-left: 300px;">
    <!---->

    <v-divider></v-divider>

    <!---->
    <v-card-text class="text-left">
      <v-row>
        <v-card-title></v-card-title>
        <v-card-title>{{this.username+'sdgsdg'}}</v-card-title>
      </v-row>

      <v-row>
        <v-card-title></v-card-title>
        <v-card-title>{{blognum}}</v-card-title>
      </v-row>

      <v-row>
      <v-card-title v-show="!userInfoEditShow"></v-card-title>
      </v-row>
      <v-card-text v-show="!userInfoEditShow">{{}}</v-card-text>
      <v-row>
      <v-card-title v-show="userInfoEditShow"></v-card-title>
      </v-row>
      <v-textarea v-model="newintro" v-show="userInfoEditShow"
                  :rules="[rules.intromax]"></v-textarea>


      <!---->
      <v-row>
        <v-card-title></v-card-title>
        <v-row v-show="userInfoEditShow">
          <v-col cols="3">
            <v-select v-model="gendervalue" :items="newgender"></v-select>
          </v-col>
        </v-row>
      </v-row>

      <!---->
      <v-row>
        <v-card-title></v-card-title>
        <v-card-title>{{birthday}}</v-card-title>
      </v-row>
        <!---->
      <v-row>
        <v-sheet
          v-show="userInfoEditShow" tile height="54"
          color="grey lighten-3">
          <v-row>
            <v-btn icon class="ma-2" @click="$refs.calendar.prev()">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn icon class="ma-2" @click="$refs.calendar.next()">
              <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
          </v-row>
        </v-sheet>
        <!---->
        <v-calendar v-show="userInfoEditShow"
                    ref="calendar"
                    v-model="birthday"
                    :type="'month'"
                    :event-overlap-mode="'stack'"
                    :event-overlap-threshold="30">
        </v-calendar>
      </v-row>

      <!---->
      <v-row>
        <v-card-title></v-card-title>
        <img :src="'/user/getavatar?username='+this.username"
             ref="userpicplace" alt="userimg" width="200" height="200"/>

      </v-row>
      <v-row style="padding-left: 30px">
        <v-btn v-show="userInfoEditShow">
            <input
              style="position:absolute; width: 70px;"
              @change="changepic($event)"
              id="file1" type="file" ref="newpic"
              accept="image/*"/>
        </v-btn>
      </v-row>
    </v-card-text>

    <!---->
    <v-row>
    <v-btn @click="userInfoEditShow=!userInfoEditShow" v-show="!userInfoEditShow"
           color="primary" large absolute right></v-btn>

    <v-btn @click="savePersonalInfo" v-show="userInfoEditShow"
           style="margin-right: 90px"
           large absolute right :disabled="!updateInfoCheck"></v-btn>
    <v-btn @click="userInfoEditShow=!userInfoEditShow" v-show="userInfoEditShow"
           large absolute right></v-btn>
    </v-row>
  </v-card>
</template>

<script>
    export default {
        name: "userInfo",
      data:()=>({
        //
        pickedpic:'../../static/pics/study.png',//
        userInfoEditShow:false,//
        newpicFile:'',
        newpicName:'',
        newsrc:'',

        //
        rules: {
          intromax: function (intro) {
            if (intro.length>=200){
              return ('200')
            }
            else return true
          }
        },

        //
        newintro:'',//
        newgender:[
          '','','',
        ],
        gendervalue:'',
        genderPtr:0,//
        newBlogTitle:'',

        //
        picSrc:'',
        userinfolist:'',
        username:'',//
        userimgUrl:'',//url
        birthday: 'unknown',//
        blognum:0,//
        usertype:'',//
      }),
      mounted() {
        //
        /*
        this.$axios.post('/user/getcurrentuser',{})
        .then(function (response) {
          if (response.state === 'success'){
            this.username = response.username
          }
          else alert('')
        })
        .catch(function (error) {
          console.log(error)
        })

        if (this.username !== ''){
          this.$axios.post('/user/getcurrentuser',{})
            .then(function (response) {
              if (response.state === 'success'){
                this.userinfolist = response.info
              }
              else alert(response.message)
            })
            .catch(function (error) {
              console.log(error)
            })
        }

         */


      },
      computed:{
        updateInfoCheck(){
          return (this.newintro.length<200)
        },
      },
      methods: {
        //
        changepic(event) {
          //changepic(this)
          //
          let newPic = event.target.files;
          this.newpicName = newPic[0].name
          this.newpicFile = this.$refs.newpic.files[0];
          var newsrc = this.getObjectURL(event.target.files[0]);//url
          this.$refs.userpicplace.src = newsrc;

          //

        },

        //url
        getObjectURL(file) {
          var url = null ;

          //  js 
          if (window.createObjectURL!=undefined) { // basic
            url = window.createObjectURL(file) ;
          } else if (window.URL!=undefined) { // mozilla(firefox)
            url = window.URL.createObjectURL(file) ;
          } else if (window.webkitURL!=undefined) { // webkit or chrome
            url = window.webkitURL.createObjectURL(file) ;
          }
          return url ;
        },

        openPic(){
          this.$refs.newpic.click()
        },

        //
        savePersonalInfo(){
          //
          this.$axios.post('/api/updateinfo',{
            desc:this.newintro,//
            gender:this.newgender.indexOf(this.gendervalue),//index
            //email:
          }).then(function (response) {
            if (response == true){
              alert('')
              //
              this.$axios.post('/user/getinfo',{
                username: this.username
              }).then(function (response) {
                if (response.state == true){
                  this.userinfolist = response
                }
                else alert('')
              })
              //
              this.userInfoEditShow = !this.userInfoEditShow
            }
            else alert('')
          }).catch(function (error) {
            console.log(error)
          })

          //
          this.$axios.post('/api/updateinfo',{
            desc:this.newintro
            //gender:
            //email:
          },{
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }).then(function (response) {
            if (response == true){
              alert('')
              //
              this.$axios.post('/user/getavatar',{
                avatar:this.newpicFile
              }).then(function (response) {
                if (response.state == true){
                  this.userinfolist = response
                }
                else alert('')
              })
              //
              this.userInfoEditShow = !this.userInfoEditShow
            }
            else alert('')
          }).catch(function (error) {
            console.log(error)
          })

        },
      }
    }
</script>

<style scoped>

</style>



